	module.exports = {"data":{"markdownRemark":{"html":"<p>Bergenseren og tenoren Thorbjørn Gulbrandsøy var ansatt som solist ved Den Norske Opera &#x26; Ballett 2015-2017 der han bland annet har sunget Tamino i Tryllefløyten, Styrmannen i Den Flyvende Hollender, Andres i Wozzek og Beppe i Pagliacci, og er gjeninvitert i sesongene som venter. I 2008 sang Guldbrandsøy Pelléas i Independent Operas produksjon av Pelléas et Mélisande på Sadlers Wells Theatre i London. I 2011/12 sesongen sang han sin første Rodolfo i La Bohème, samt tenor-partiet i Haydns Skapelsen.</p>\n<p>Han har bland annet sunget ved Nationale Reisopera i Nederland, Bergen Nasjonale Opera og ved Staatsoper Berlin. I 2015 var Gulbrandsøy engasjert til å synge Romeo i Gounods Romeo og Julie på turné med Riksteatern i Sverige. Samme år sang han tenorpartiet i Mozarts requiem med Oslo-filharmonien der han var tilbake høsten 2016 som solist i Boulanger’s Faust et Hèlén og i mai 2017 i Brecht/ Weill’s De syv dødssynder.</p>\n<p>Hovedrollene som Rodolfo i La Bohème venter ved Opera Nordfjord, Hertugen i Rigoletto ved operafestukene i Kristiansand og Tamino i Tryllefløyten ved DNO&#x26;B i sesongen 2018/2019.</p>\n<p>Som Lieder-sanger skal han fremføre både Brahms «Liebeslieder» og Schumanns «Spanische Liebeslieder» sammen med Isa Katharina Gericke, Melis Jaatinen, Halvor Melien og pianistene Thormod Rønning Kvam og Juho Alakärppä ved Nordsnorsk Opera og Symfoniorkester i Bodø og Tromsø og på Operaen i neste sesong.</p>\n<p>Han er også invitert til å synge Beethovens berømte sangsyklus «An die ferne Geliebte» m.m. ved Engegårdskvartettens Beethovenfestival i november 2018.</p>","timeToRead":1,"excerpt":"Bergenseren og tenoren Thorbjørn Gulbrandsøy var ansatt som solist ved Den Norske Opera & Ballett 2015-2017 der han bland annet har sunget…","id":"/Users/thomasheimstad/Sites/Obo/src/posts/artister/thorbjorn-gulbrandsoy.md absPath of file >>> MarkdownRemark","frontmatter":{"workname":null,"composedin":null,"startDate":null,"startTime":null,"location":null,"artists":null,"medialink":null,"title":"Thorbjørn Gulbrandsøy","dedicatedTo":null,"author":null,"date":"2018-05-07T12:00:00.048Z","category":"artister","tags":["sanger"],"thumbnail":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.9222163026105488,"src":"/static/ThorbjornGulbrandsoy-104b8b1ba3cdc7ee8358ba89f68672c0-47774.jpg","srcSet":"/static/ThorbjornGulbrandsoy-104b8b1ba3cdc7ee8358ba89f68672c0-45ad6.jpg 480w,\n/static/ThorbjornGulbrandsoy-104b8b1ba3cdc7ee8358ba89f68672c0-e2fcf.jpg 960w,\n/static/ThorbjornGulbrandsoy-104b8b1ba3cdc7ee8358ba89f68672c0-47774.jpg 1920w,\n/static/ThorbjornGulbrandsoy-104b8b1ba3cdc7ee8358ba89f68672c0-93088.jpg 2880w,\n/static/ThorbjornGulbrandsoy-104b8b1ba3cdc7ee8358ba89f68672c0-4938b.jpg 3608w","sizes":"(max-width: 1920px) 100vw, 1920px"}}}},"fields":{"slug":"/artister/thorbjorn-gulbrandsoy/","artist":null,"opuses":[{"frontmatter":{"title":"26","category":"opus","workname":"Fem dikt av John Paulsen","thumbnail":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1,"src":"/static/opus26-a96aedb13b52ca3b09c7897b92143c59-466fa.jpg","srcSet":"/static/opus26-a96aedb13b52ca3b09c7897b92143c59-27b23.jpg 90w,\n/static/opus26-a96aedb13b52ca3b09c7897b92143c59-0afbd.jpg 180w,\n/static/opus26-a96aedb13b52ca3b09c7897b92143c59-466fa.jpg 360w,\n/static/opus26-a96aedb13b52ca3b09c7897b92143c59-43088.jpg 540w,\n/static/opus26-a96aedb13b52ca3b09c7897b92143c59-545bd.jpg 720w,\n/static/opus26-a96aedb13b52ca3b09c7897b92143c59-8c1c4.jpg 1080w,\n/static/opus26-a96aedb13b52ca3b09c7897b92143c59-ec42f.jpg 1994w","sizes":"(max-width: 360px) 100vw, 360px"}}}},"fields":{"slug":"/opus26/","artist":[{"frontmatter":{"title":"Thorbjørn Gulbrandsøy","tags":["sanger"]}},{"frontmatter":{"title":"Knut Christian Jansson","tags":["pianist"]}}]}}]}}},"pathContext":{"slug":"/artister/thorbjorn-gulbrandsoy/"}}

